.Home {
    text-align: center;
  }
  
  .Home-logo {
    animation: App-logo-spin infinite 15s linear;
    height: 35vmin;
    pointer-events: none;
  }
  
  .HomeScreen {
    padding: 100px;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }